body {
  text-align: center;
  background-color: #011f3f;
}

#level-title {
  font-family: 'Press Start 2P', cursive;
  font-size: 3rem;
  margin: 5%;
  color: #fef2bf;
}

.container {
  display: block;
  width: 50%;
  margin: auto;
}

.btn {
  margin: 25px;
  display: inline-block;
  height: 200px;
  width: 200px;
  border: 10px solid black;
  border-radius: 20%;
}

.game-over {
  background-color: red;
  opacity: 0.8;
}

.red {
  background-color: red;
}

.green {
  background-color: green;
}

.blue {
  background-color: blue;
}

.yellow {
  background-color: yellow;
}

.pressed {
  box-shadow: 0 0 20px white;
  background-color: grey;
}
#start-button {
  background-color: #fef2bf;
  font-family: 'Press Start 2P', cursive;
  height: 2rem;
  width: 10rem;
  color: #011f3f;
  margin: 2rem 0;
  border-style: none;
  border-radius: 2%;
}
#start-button:hover {
  color: #b9cce1;
  background-color: #504100;
}
.inactive {
  pointer-events: none;
}
.hide {
  display: none;
}
